.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  
    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  
  @for $childCount from 1 through 5 {
    &--#{$childCount} &__star:nth-child(-n + #{$childCount}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
